:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body {
  font-family: 'Noto Serif';
  color: #4e4e4e;
  color: var(--secondary);
}
.App {
  max-width: 960px;
  margin: 0 auto;
}
.title h1 {
  color: #efb6b2;
  color: var(--primary);
  font-size: 1.8rem;
  letter-spacing: 2px;
  font-weight: normal;
}

