.collection-page{margin-top:30px;font-family:"Dancing Script"}.collection-page .title{margin-bottom:40px;color:#a98b98;background-color:#f9f3f3;font-size:0.5cm;font-family:"Orbitron";text-align:center}

.collection-item{width:22%;display:flex;flex-direction:column;height:350px;align-items:center;cursor:pointer}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .collection-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:20px;margin-top:25px;color:pink}.collection-preview .preview{display:flex;justify-content:space-between}

.homepage .feedback{padding-top:20px;font-size:0.6cm;color:#a98b98;font-family:"Orbitron";background-color:#f9f3f3;border-color:#f9f3f3;cursor:pointer;border-radius:15px;padding-bottom:20px;padding-left:80px;padding-right:80px}.homepage .repl{margin-top:50px}.homepage .wrapper{margin-top:1cm;text-align:center}

.contactpage .university{width:100%;height:100%;align-items:center;font-size:18px;margin-top:250px}.contactpage .university .info{align-items:flex-start;font-size:18px}

.button{border:0;background:none;height:5%;width:2%;cursor:pointer}.textbutton{background:none;cursor:pointer}

ul{font-size:25px;font-weight:bold}.pro{font-style:italic;-webkit-text-decoration-line:underline;text-decoration-line:underline;font-size:0.6cm}table{width:80%;margin-left:auto;margin-right:auto}tr{vertical-align:middle;text-align:center;border:3px solid black}td{width:3%}dt,dd{font-size:15px;text-align:start;font-weight:normal}.textsound{color:#0534cf;text-decoration:underline}.textdl{font-size:15px;font-weight:lighter;text-align:left;word-spacing:100%}li{padding-top:0.5cm}.town{background-color:#759e9e}.home{background-color:#8696ac}.family{background-color:#cfd3ae}.restaurant{background-color:#d3bcae}.textdl{margin-left:auto;margin-right:auto}

